  getters: {

  },
  mutations: {
    removeSelectedItem:  function(state, item) {
      const index = state.selected_targets.indexOf(item)
      if (index > -1) {
        state.selected_targets.splice(index, 1);
      } 
    },
    clickItem: function(state,item){
      if (state.selected_targets.includes(item)) {
        const index = state.selected_targets.indexOf(item)
        if (index > -1) {
          state.selected_targets.splice(index, 1);
        } 
      } else {
          state.selected_targets.push(item)
      }
    },
    selectAll: function(state, data) {
      var items = data.items
      var clear = data.clear
      if (clear) {
        state.selected_targets = state.selected_targets.filter((selected_item) => !data.items.includes(selected_item))
        
      } else {
        for (var target in items) {
          if (state.selected_targets.indexOf(items[target]) === -1) {
            state.selected_targets.push(items[target]);
          }
        }
      
      }
    }, 
    updateSelectedTarget: function(state, value){
      state.selected_targets = value
    },
    addQueryResponse: function(state, data) {
      const index = state.query_objects.indexOf(data.obj)
      state.query_objects[index].response = data.response
    }

  },
  actions: {
  },
