<template>
  <div>
    <div >
      <input type="text" placeholder="search filters..." v-model="search" autofocus="true">
    </div>
    <div>
      <div :v-model="toggleAll">
        <button @click="toggleAll(filteredList)">Toggle All</button>
      </div>
      <div :v-model="selectAll">
        <label for="selectAll">Select All</label>
        <input type="checkbox" id="selectAll" @click="selectAll(filteredList)" v-model="allSelected">
      </div>
      <div :v-model="clearAll">
        <label for="selectAll">Clear All</label>
        <input type="checkbox" id="selectAll" @click="clearAll(filteredList)" v-model="noneSelected">
      </div>
    <div>{{this.$store.state.selected_targets.length}} SELECTED</div>
    </div>

    <div  class="scrollable" style="border: 1px solid steelblue; padding: 5px; margin: 2px; height: 200px">
      <ul id="customScroll" data-simplebar-auto-hide="false">
        <div v-for="(filter, index) in filteredList" :key="index" @click="this.$store.commit('clickItem', filter)"  >

          <input :value="filter" v-model="selectedTargets" type="checkbox">
          <label >{{filter.Label}}</label >
          <label style="float: right;">{{filter.Target_ID}}</label>

        </div>
      </ul>
    </div>
    <p>Selected: </p>
    
    <div v-for="(filter, index) in this.$store.state.selected_targets" :key="index" @click="this.$store.commit('clickItem', filter)" class="small-box" :data-tooltip=filter.Beschrijving >
      <input :value="filter" v-model="selectedTargets" type="checkbox">
      <label>{{filter.Label}}</label>      
    </div>
  </div>

</template>

<script>
export default {
  name: 'SearchableList',
  props: {
  },
  data() {
    return {
      search: "",
      dropdown: true,
    };
  },
  computed: {
    searchList() {
      return this.search.toLowerCase().split(',');
    },
    filteredList() {
      return this.$store.state.target_data.filter((item) => 
        this.searchList.some((searchItem) => item.Label.includes(searchItem))
        ||
        this.searchList.some((searchItem) => item.Target_ID.toString() == searchItem)
      
      );
    },
    selectAllText() {return this.allSelected ? 'Clear All' : 'Select All';},
    allSelected: {
      get() {return this.filteredList.every((filtered_item) => this.$store.state.selected_targets.includes(filtered_item)) },
      set(val){}
    },
    noneSelected: {
      get() {return this.filteredList.every((filtered_item) => !this.$store.state.selected_targets.includes(filtered_item)) },
      set(val){}
    },
    selectedTargets: {
      set(value){this.$store.commit('updateSelectedTarget', value)},
      get(){ return this.$store.state.selected_targets }
    }
  },
  methods: {
    showDropdown: function(){
        this.dropdown = !this.dropdown; 
      }, 
    toggleAll: function(items) {
      const all_selected = this.allSelected
      this.$store.commit('selectAll', {'items':items,'clear': all_selected})
    },
    selectAll: function(items) {
      this.$store.commit('selectAll', {'items':items,'clear': false})
    },
    clearAll: function(items) {
      this.$store.commit('selectAll', {'items':items,'clear': true})
    }
  },
}
</script>

<style scoped>
.small-box{
  float: left; 
  margin-left: 10px; 
  box-sizing: content-box; 
  border: 2px solid steelblue;
  padding-left: 3px;
  padding-right: 3px;
background: lightsalmon;
}
.scrollable{
  overflow-y: scroll;
}
</style>